body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Montserrat",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1rem;box-sizing:border-box}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.todo{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:10rem 0}.todo__title{font-size:2.5rem;text-align:center;margin:1.3rem 0}.todo__inner{border:1px solid #2b2a2c;border-radius:2rem}.todoitem__new-input{display:inline-block;position:relative;border:1px solid #6a696b;transition:all .4s ease}.todoitem__new-input:hover{border-color:#2b2a2c;transition:all .2s ease;background-color:rgba(124,252,0,0.13)}.container{display:flex;user-select:none}.container input{opacity:0;height:0;width:0}.container input:checked+.todoitem__new-input{transition:all .4s ease;background-color:lawngreen}.todoitem__new-input:after{content:"";position:absolute;display:none}.container input:checked+.todoitem__new-input:after{display:block}.container .todoitem__new-input:after{left:4.6px;bottom:3px;width:4px;height:10px;border:solid #fff;border-width:0 3px 3px 0;transform:rotate(45deg)}.todolist__clear{text-align:center;font-weight:300}.modal__close{z-index:1;outline:none;border:none;background-color:transparent;border-radius:50%}.modal__pleace-img{border:1px solid #2b2a2c;border-radius:50%;transition:all .7s ease;transform:scale(.95)}.modal__pleace-img:hover{background-color:red;box-shadow:0 0 2px #2b2a2c;transform:scale(1.1)}.modal__pleace-img:hover .modal__close-img-icon{fill:#fff;background-image:red}.modal__pleace-img:hover .modal__close-img{transform:rotate(180deg)}.modal__close-img{transition:all .7s ease;display:flex;height:17px;width:17px;padding:.3rem;cursor:pointer}.modal__close-img-icon{transition:ease .7s all;transform:scale(1)}.done{text-decoration:line-through}.form{display:flex;align-items:center;justify-content:center}.form__inner{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%}.form__label{font-size:.8rem}.form__input{font-size:12px;width:10rem;border-radius:50px;border:1px solid #8d8d8e;transition:all .3s ease;padding:.6rem}.form__input:focus{outline:none;border:1px solid #2b2a2c}.form__input:hover{border:1px solid #2b2a2c}.form__input:focus::placeholder{color:#2b2a2c;transform:translateY(-10px);font-size:10px}input::placeholder{transition:all .3s ease}.form__button{background:none;border:1px solid #2b2a2c;border-radius:50px;padding:.6rem;font-family:inherit;font-weight:400;transition:all .3s ease;cursor:pointer;width:8rem}.form__button:hover{background:#2b2a2c;color:#fff;box-shadow:0 0 3px rgba(43,42,44,0.5)}.form__padding{margin:.3rem}